
.main-nav .tap:after{background-position:center center;background-repeat:no-repeat
}
.main-nav{position:relative;height:30px;width:100%;background-color:#5d9cec
}
.main-nav ul,.main-nav li{padding:0;margin:0;list-style:none
}
.main-nav .main{width:90%;height:30px;margin:0 auto
}
.main-nav .menu-cell{display:inline-block;text-align:center;min-width:80px
}
.main-nav .menu-cell:hover{background-color:#5C676D
}
.main-nav .menu-cell:hover .subbox{display:block
}
.main-nav .tap{position:relative;line-height:30px;color:#fff;padding:0 20px 0 10px;box-sizing:border-box;display:block;text-align:center;cursor:pointer;width:100%;text-overflow:ellipsis
}
.main-nav .tap:after{background-image:url(../../../assets/menu.png);content:'';position:absolute;top:0;right:0;width:20px;height:30px
}
.main-nav .menu-cell:hover .tap:after{background-image:url(../../../assets/menu_hover.png)
}
.main-nav .menu-cell .tapnomore{padding:0 10px
}
.main-nav .main .menu-cell .tapnomore:after{background-image:none
}
.main-nav .sub-tap{display:block;text-align:left;cursor:pointer;width:100%;text-overflow:ellipsis;font-size:14px;line-height:1.6em;border-bottom:2px solid #5d9cec
}
.main-nav .ssub-tap{display:block;text-align:left;cursor:pointer;width:100%;text-overflow:ellipsis;display:block;margin:5px 0
}
.main-nav .subbox{display:none;position:absolute;top:100%;left:0;z-index:10;width:100%;background-color:#f5f5f5
}
.main-nav .list-box{text-align:left
}
.main-nav .sub-cell{max-width:14em;padding:1em;display:inline-block;vertical-align:top
}
.main-nav .sub-tap:hover,.main-nav .ssub-tap:hover{color:#1CCCFE
}



